.carousel {
  overflow: hidden;
  color: white;
  position: relative;
  height: 100vh
}

.box {
  position: relative;
}

.carousel__item {
  display: inline-flex;
  height: 100%;
  position: relative;
  width: 100%;
  position: relative;
}

.carousel__image {
  object-fit: cover;
  z-index: -1;
  width: 100%;
}

.carousel__inner {
  white-space: nowrap;
  transition: transform 1.5s ease; 
  position: relative;
  height: 100%;
}

.carousel_button {
  position: absolute;
  bottom: 85px;
  right: 100px;
  height: 50px;
  width: 120px;
  font-family: 'Inter';
  font-style: normal;
  /* font-weight: 500; */
  background-color: #3DDC84;
  background-color: #1D96F1;
  color: #000000;;
  border: none;
  border-radius: 5px;
  transition: opacity 0.4s linear;
  cursor: pointer;
  display: inline-block;
  opacity: 0.8;
  font-size: 12px;
  font-weight: 600;
}

.carousel_button:hover {
  opacity: 0.9;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.315);
}

.carousel_buttonText {
  margin: 0;
  letter-spacing: 0.8px;
  font-family: 'mont_extralightdemo';
  font-family: 'Mont-Regular';
}

.carousel__photo {
  height: 100%;
  display: flex;
}

.carousel__text {
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.29;
  width: 450px;
  white-space: normal;
  margin: 0 0 5px;
  line-height: 1.4;
  letter-spacing: 0.8px;
  font-family: 'Montserrat-Regular';
  font-family: 'Montserrat-Medium';
}

.carousel__textContainer {
  position: absolute;
  top: 110px;
  left: 100px;
}

.carousel__textContainer_type_second {
  /* top: 590px; */
  bottom: 120px;
  top: auto;
}

.carousel__textContainer_type_third {
  top: 120px;
  left: 290px;
}

.carousel__wrapButton {
  z-index: 2;
  position: absolute;
  top: calc((50% - 35px));
  height: 70px;
  width: 70px;
  border: none;
  background: transparent;
  opacity: 0;
  transition: opacity ease 1.5s;
  cursor: pointer;
}

.carousel:hover .carousel__wrapButton {
  opacity: 1;
}

.carousel__wrapButton_type_next {
  right: 0px;
  background-image: url(../../images/arrow_right.svg);
}

.carousel__wrapButton_type_prev {
  left: 0px;
  background-image: url(../../images/arrow_left.svg);
}

.carousel__wrapButton_hidden {
  visibility: hidden;
}

@media screen and (max-width: 1279px) {
  .carousel__text {
    font-size: 16px;
    width: 350px;
  }

  .carousel__textContainer {
    left: 70px; 
  }
  
  .carousel__textContainer_type_third {
    left: 170px;
  }  

  .carousel__wrapButton {
    height: 65px;
    width: 65px;
  }

  .carousel_button {
    bottom: 75px;
    right: 90px;
  }
}

@media screen and (max-width: 1024px) {
  .carousel__text {
    font-size: 14px;
  }

  .carousel {
    height: 55vh;
  }

  .carousel__textContainer {
    top: 90px; 
  }

  .carousel__textContainer_type_second {
    bottom: 80px;
    top: auto;
  }

  .carousel__wrapButton {
    height: 55px;
    width: 55px;
  }

  .carousel_button {
    bottom: 65px;
    right: 80px;
  }
}

@media screen and (max-width: 899px) {
  .carousel__text {
    font-size: 13px;
  }
}

@media screen and (max-width: 767px) {
  .carousel {
    height: 100vh
  }
  .carousel_button {
    bottom: 50px;
    right: 30px;
    left: 30px;
    height: 50px;
    opacity: 0.9;
    width: calc(100vw - 60px);
  }
  .carousel_buttonText {
    letter-spacing: 1.2px;
    font-family: 'Mont-Regular';
    font-style: normal;
    font-size: 12px;
    text-transform: uppercase;
  }
}



